/* Typography*/
@import url("https://fonts.googleapis.com/css?family=Karla:400|Spectral:400");
/* alerts  onloading animations */
@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}
.loading {
  width: 6rem;
  height: 6rem;
  margin: 0 auto;
  border: 5px solid #94a3b8;
  border-radius: 50%;
  border-top-color: #645cff;
  animation: spinner 0.6s linear infinite;
}

html {
  font-size: 100%;
}

/*16px*/
body {
  display: grid;
  place-items: center;
  min-height: 100vh;
  background: #eaebf5;
  font-family: Helvetica Neue, sans-serif;
  font-weight: 400;
  line-height: 1.3;
  color: #0f172a;
}

p {
  max-width: 40em;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  margin-bottom: 1.38rem;
  line-height: 1.3;
  text-transform: capitalize;
}

h1 {
  margin-top: 0;
  font-size: 3.052rem;
}

h2 {
  font-size: 2.441rem;
}

h3 {
  font-size: 1.953rem;
}

h4 {
  font-size: 1.563rem;
}

h5 {
  font-size: 1.25rem;
}

small,
.small-Text {
  font-size: 0.9rem;
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
  padding: 0;
}

/* buttons */
.btn {
  cursor: pointer;
  color: white;
  background: #645cff;
  border: transparent;
  border-radius: 0.25rem;
  letter-spacing: 1px;
  padding: 0.75rem 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  transition: 0.3s ease-in-out all;
  text-transform: capitalize;
  text-align: center;
  display: inline-block;
  /*block class is good with forms*/
}
.btn:hover {
  background: #3c3799;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}
.btn-hipster {
  color: #645cff;
  background: #c0bdff;
}
.btn-hipster:hover {
  color: #c0bdff;
  background: #3c3799;
}
.btn-block {
  width: 100%;
}

/* alerts */
.alert {
  padding: 0.375rem 0.75rem;
  margin-bottom: 1rem;
  border-color: transparent;
  border-radius: 0.25rem;
}

.alert-errors {
  color: #842029;
  background: #f8d7da;
}

.alert-success {
  color: #0f5132;
  background: #d1e7dd;
}

/* forms */
.form {
  padding: 2rem 2.5rem;
}
.form .form-group {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 0.5rem;
  padding-bottom: 1rem;
}
.form .form-group .form-label {
  display: inline-block;
  margin-left: 1.5rem;
  font-size: 0.9rem;
  font-weight: 700;
  text-transform: capitalize;
  letter-spacing: 1px;
}
.form .form-group .form-input {
  width: 100%;
  padding: 0.75rem 1.5rem;
  border-radius: 0.25rem;
  background: #f8fafc;
  border: 1px solid #e2e8f0;
}
.form .form-group .form-input ::placeholder {
  font-family: inherit;
  color: #94a3b8;
}
.form .form-group .form-alert {
  color: #842029;
  letter-spacing: 1px;
  text-transform: capitalize;
  margin-left: 1.5rem;
  visibility: hidden;
}
.form .form-group.success .form-input {
  border-color: #0f5132;
}
.form .form-group.error .form-input {
  border-color: #842029;
}
.form .form-group.error .form-alert {
  visibility: visible;
}
.form .btnFooter {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}

/* titles */
.title {
  text-align: center;
}

.title-underline {
  background: #645cff;
  width: 7rem;
  height: 0.25rem;
  margin: 0 auto;
  margin-top: -1rem;
}

/* images */
.imgContainer {
  width: 40vw;
  max-width: 1120px;
  border-radius: 0.25rem;
}
.imgContainer .img {
  width: 100%;
  display: block;
  object-fit: cover;
}

.signUp {
  display: grid;
  place-items: center;
  gap: 0.625rem;
}

.header {
  padding: 1.25rem 2.5rem;
}
.header h1 {
  font-size: 26px;
  line-height: 32px;
  margin: 0;
  text-transform: initial;
}

*,
::after,
::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.container {
  width: 90%;
  max-width: 470px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  border-radius: 1.5rem;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}